.message {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    transform: scaleX(-1);
}

.imageAndText {
    display: flex;
    gap: 27px;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {
    padding: 8px 13px;
    box-shadow:
            0 1px 2px rgba(29, 33, 38, 0.1),
            0 5px 20px rgba(29, 33, 38, 0.03);
    background: #0066CC;
    color: #fff;
    border-radius: 14px;
    display: flex;
    flex-direction: column;
    gap: 4px;
    position: relative;

}

.text:after {
    content: '';
    position: absolute;
    left: -12px;
    bottom: 0px;
    width: 25px;
    height: 20px;
    background: #0066CC;

    clip-path: polygon(100% 0, 0 100%, 100% 100%);
    filter: drop-shadow(0 1px 2px rgba(29, 33, 38, 0.1))
    drop-shadow(0 5px 20px rgba(29, 33, 38, 0.03));
}

.name {
    font-weight: 600;
    font-size: 16px;
    color: white;
    transform: scaleX(-1);
}

.messageText {
    font-weight: 200;
    font-size: 16px;
    color: rgba(255, 255, 255, 0.8);
    transform: scaleX(-1);
}

.time {
    font-weight: 600;
    font-size: 10px;
    color: #000;
    width: 48px;
    text-align: center;
    transform: scaleX(-1);
}