.sendForm {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 44px;
    margin-top: 30px;
}

.textarea {
    width: 100%;
    height: 40px;
    border-radius: 20px;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
    background: #f5f7fb;
    font-weight: 400;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.6);
    padding: 10px 24px;
    border: none;
    resize: none;
    outline: none;
}

.textarea:focus {
    border: 1px solid #0066CC;
    overflow: hidden;
}

.button {
    border-radius: 20px;
    width: 120px;
    height: 40px;
    background: #06c;
    font-weight: 600;
    font-size: 16px;
    color: #fff;
    border: 1px solid #0066CC;
    transition:
            background-color 0.5s ease,
            color 0.5s ease,
            border-color 0.5s ease;
}

.button:hover {
    border: 1px solid #0066CC;
    background: white;
    color: #06c;

}

.button:active {
    transform: scale(0.9);
}
