.friendMessage {
    display: flex;
    flex-direction: column;
}

.friendImageAndText {
    display: flex;
    gap: 27px;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
}

.friendText {
    padding: 8px 13px;
    box-shadow:
            0 1px 2px rgba(29, 33, 38, 0.1),
            0 5px 20px rgba(29, 33, 38, 0.03);
    background: #f5f7fb;
    border-radius: 14px;
    display: flex;
    flex-direction: column;
    gap: 4px;
    position: relative;
}

.friendText ::before {
    content: '';
    position: absolute;
    left: -12px;
    bottom: 0px;

    width: 25px;
    height: 20px;
    background: #F5F7FB;

    clip-path: polygon(100% 0, 0 100%, 100% 100%);
    filter: drop-shadow(0 1px 2px rgba(29, 33, 38, 0.1))
    drop-shadow(0 5px 20px rgba(29, 33, 38, 0.03));
}

.friendName {
    font-weight: 600;
    font-size: 16px;
    color: #000;
}

.friendMessageText {
    font-weight: 200;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.7);
}

.friendTime {
    font-weight: 600;
    font-size: 10px;
    color: #000;
    width: 48px;
    text-align: center;
}
