@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    flex-direction: column;
    width: 100%;
    padding: 130px 0 30px 0;
}

.App {
    --header_height: 60px;
}

.hw {
    min-width: 100%;
    padding: 25px 0 35px 0;
    position: relative;
}

.hw::after,
.hw::before {
    content: '';
    position: absolute;
    width: 100vw;
    height: 1px;
    background-color: #D9D9D9;
    left: 50%;
    transform: translateX(-50%);
}

.hw::after {
    top: 0;
}

.hw::before {
    bottom: 0;
}

.hwTitle {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 22px;
    color: #000;
    margin-bottom: 10px;
}

.messageArea {
    width: 1140px;
}

